import React, { useEffect } from 'react';
import { createPortal } from 'react-dom';
import closeIcon from '../assets/images/close-icon.svg';

export default function Popup({ show, toggleShow, children }) {
  useEffect(() => {
    const body = document.querySelector('body');
    show
      ? body.classList.add('overflow-hidden')
      : body.classList.remove('overflow-hidden');

    document.addEventListener('keydown', (e) => {
      if (e.key.toLowerCase() === 'escape' && show) toggleShow();
    });

    return () => body.classList.remove('overflow-hidden');
  }, [show, toggleShow]);

  return !show
    ? null
    : createPortal(
        <>
          <div className="popup">
            {children}
            <button onClick={toggleShow} type="buton" className="popup-close">
              <img src={closeIcon} alt="" />
            </button>
            <div onClick={toggleShow} className="popup-backdrop" />
          </div>
        </>,
        document.querySelector('#modal-root')
      );
}
