import React, { useEffect } from 'react';
import anime from 'animejs/lib/anime';

const animOptions = {
  duration: 1500,
  direction: 'alternate',
  easing: 'linear',
  loop: true,
  autoplay: false,
};

export default function Bubble18({ isAnimate, toggleBubbleAnimation }) {
  useEffect(() => {
    const anime1 = anime({
      ...animOptions,
      targets: '#bubble-18-bg path',
      d: [
        {
          value:
            'M175.000,10.000 C206.187,9.199 228.019,4.332 240.000,23.000 C253.511,44.052 238.381,80.469 232.000,95.000 C211.912,140.744 167.144,235.172 94.000,205.000 C51.977,187.665 14.715,135.692 2.000,89.000 C-5.076,63.014 8.007,29.818 25.000,17.000 C55.728,-6.178 72.261,0.238 107.000,4.000 C111.270,4.462 168.127,12.652 175.000,10.000 Z',
        },
      ],
    });

    if (isAnimate) {
      anime1.play();
    } else {
      anime.remove('#bubble-18-bg path');
    }
  }, [isAnimate, toggleBubbleAnimation]);

  return (
    <button type="button" className="no-btn-styles bubble bubble-18">
      <svg
        className="bubble-item bubble-18-bg"
        xmlns="http://www.w3.org/2000/svg"
        width="270px"
        height="232px"
      >
        <clipPath id="bubble-18-bg">
          <path
            fill="rgb(0, 0, 0)"
            d="M167.000,1.000 C198.187,0.199 237.019,2.332 249.000,21.000 C262.511,42.052 240.381,81.469 234.000,96.000 C213.912,141.744 159.144,226.172 86.000,196.000 C43.977,178.665 16.715,136.692 4.000,90.000 C-3.076,64.014 0.007,30.818 17.000,18.000 C47.728,-5.178 64.261,11.238 99.000,15.000 C103.270,15.462 155.332,2.706 167.000,1.000 Z"
          />
        </clipPath>
        <image
          clipPath="url(#bubble-18-bg)"
          href={require('../../assets/images/bubble-18-bg.gif')}
        />
      </svg>
      <svg
        className="bubble-item bubble-18-shape"
        xmlns="http://www.w3.org/2000/svg"
        width="169px"
        height="87px"
      >
        <path
          fill="rgb(237, 49, 90)"
          d="M27.000,-0.000 C51.519,-0.380 60.098,11.290 81.000,14.000 C112.333,18.063 138.885,-7.246 163.000,20.000 C167.061,24.588 170.864,32.376 168.000,41.000 C152.318,88.228 59.986,101.203 19.000,69.000 C6.478,59.161 -10.731,20.717 9.000,6.000 C14.203,1.701 21.001,2.000 27.000,-0.000 Z"
        />
      </svg>
      <span className="bubble-item bubble-18-text">
        Special &amp; Essential
      </span>
    </button>
  );
}
