import React from 'react';
import '../styles/components/our-client-section.scss';

function importAll(r) {
  return r.keys().map(r);
}

const clientImages = importAll(
  require.context('../assets/images/clients/', false, /\.png$/)
);

const logos = clientImages.map((d, index) => (
  <div
    key={`client_img_${index}`}
    className="col-lg-2 col-md-4 col-6 mb-lg-4 mb-3"
  >
    <img className="img-fluid d-block mx-auto" src={d} alt="" />
  </div>
));

export default function OurClientSection() {
  return (
    <section className="our-client-section">
      <h2 className="text-center">Our Clients</h2>
      <div className="container">
        <div className="row align-items-center">
          {logos}
          {/* <div className="col mt-3">
            <button className="btn mx-auto d-block col-2" type="button">
              More
            </button>
          </div> */}
        </div>
      </div>
    </section>
  );
}
