import React, { useEffect } from 'react';
import anime from 'animejs/lib/anime';

const animOptions = {
  duration: 1500,
  direction: 'alternate',
  easing: 'linear',
  loop: true,
  autoplay: false,
};

export default function Bubble20({ isAnimate, toggleBubbleAnimation }) {
  useEffect(() => {
    const anime1 = anime({
      ...animOptions,
      targets: '#bubble-20-bg path',
      d: [
        {
          value:
            'M125.000,1.000 C186.642,-0.076 216.379,28.545 239.000,66.000 C274.936,125.499 244.123,209.412 195.000,227.000 C169.288,236.206 74.089,216.625 54.000,204.000 C21.237,183.410 -15.069,125.479 8.000,72.000 C21.528,40.638 40.287,38.309 75.000,28.000 C88.332,25.667 111.668,3.333 125.000,1.000 Z',
        },
      ],
    });

    if (isAnimate) {
      anime1.play();
    } else {
      anime.remove('#bubble-20-bg path');
    }
  }, [isAnimate, toggleBubbleAnimation]);

  return (
    <button type="button" className="no-btn-styles bubble bubble-20">
      <svg
        className="bubble-item bubble-20-bg"
        xmlns="http://www.w3.org/2000/svg"
        width="263px"
        height="254px"
      >
        <clipPath id="bubble-20-bg">
          <path
            fill="rgb(0, 0, 0)"
            d="M125.000,1.000 C186.642,-0.076 226.379,18.545 249.000,56.000 C284.936,115.499 244.123,189.412 195.000,207.000 C169.288,216.206 74.089,216.625 54.000,204.000 C21.237,183.410 -15.069,125.479 8.000,72.000 C21.528,40.638 50.287,18.309 85.000,8.000 C98.332,5.667 111.668,3.333 125.000,1.000 Z"
          />
        </clipPath>
        <image
          clipPath="url(#bubble-20-bg)"
          href={require('../../assets/images/bubble-20-bg.jpg')}
          x="-55"
          y="-5"
        />
      </svg>
      <svg
        className="bubble-item bubble-20-shape"
        xmlns="http://www.w3.org/2000/svg"
        width="221px"
        height="112px"
      >
        <path
          fill="rgb(219, 123, 53)"
          d="M130.000,1.000 C162.634,0.468 242.775,6.182 215.000,48.000 C205.045,62.988 182.372,67.338 162.000,70.000 C111.173,76.641 94.841,96.254 60.000,106.000 C44.855,110.236 14.345,116.460 5.000,103.000 C-7.053,85.640 8.231,63.290 16.000,53.000 C32.647,30.951 58.342,14.765 89.000,7.000 C102.665,5.000 116.335,3.000 130.000,1.000 Z"
        />
      </svg>
      <span className="bubble-item bubble-20-text">The Desi Groove</span>
    </button>
  );
}
