import React, { useEffect } from 'react';
import handleViewport from 'react-in-viewport';
import Bubble9 from './Bubbles/Bubble9';
import Bubble10 from './Bubbles/Bubble10';
import Bubble11 from './Bubbles/Bubble11';
import Bubble12 from './Bubbles/Bubble12';
import Bubble13 from './Bubbles/Bubble13';
import Bubble14 from './Bubbles/Bubble14';
import Bubble15 from './Bubbles/Bubble15';
import Bubble16 from './Bubbles/Bubble16';
import Bubble17 from './Bubbles/Bubble17';
import Bubble18 from './Bubbles/Bubble18';
import Bubble19 from './Bubbles/Bubble19';
import Bubble20 from './Bubbles/Bubble20';
import Bubble21 from './Bubbles/Bubble21';
import Bubble22 from './Bubbles/Bubble22';
import Bubble23 from './Bubbles/Bubble23';
import Bubble24 from './Bubbles/Bubble24';
import Bubble25 from './Bubbles/Bubble25';
import Bubble26 from './Bubbles/Bubble26';
import Bubble27 from './Bubbles/Bubble27';
import Bubble28 from './Bubbles/Bubble28';
import '../styles/components/bubbles-section.scss';
import { setAnimationDelay } from '../utils/helpers';

const bubbleGroup1 = [
  Bubble9,
  Bubble10,
  Bubble11,
  Bubble12,
  Bubble13,
  Bubble14,
  Bubble15,
  Bubble16,
];

const bubbleGroup2 = [
  Bubble17,
  Bubble18,
  Bubble19,
  Bubble20,
  Bubble21,
  Bubble22,
  Bubble23,
  Bubble24,
  Bubble25,
  Bubble26,
  Bubble27,
  Bubble28,
];
const Container = ({ className, children, forwardedRef }) => (
  <section className={`bubbles-section ${className}`}>
    <div className="container">
      <div ref={forwardedRef} className="row justify-content-between">
        {children}
      </div>
    </div>
  </section>
);

export default function BubblesSection({ toggleBubbleAnimation }) {
  const renderBubbles = (Bubble, index, inViewport, type) => (
    <div key={`bubble_${type}_${index}`} className="main-section-item">
      <Bubble
        isAnimate={inViewport}
        toggleBubbleAnimation={toggleBubbleAnimation}
      />
    </div>
  );

  const TopSectionBlock = ({ forwardedRef, inViewport }) => {
    useEffect(() => {
      if (forwardedRef.current !== null && inViewport)
        setAnimationDelay('.top-section');
    }, [forwardedRef, inViewport]);

    return (
      <Container className="top-section" forwardedRef={forwardedRef}>
        {bubbleGroup1.map((Bubble, index) =>
          renderBubbles(Bubble, index, inViewport, 'top')
        )}
      </Container>
    );
  };

  const BottomSectionBlock = ({ forwardedRef, inViewport }) => {
    useEffect(() => {
      if (forwardedRef.current !== null && inViewport)
        setAnimationDelay('.bottom-section');
    }, [forwardedRef, inViewport]);

    return (
      <Container className="bottom-section" forwardedRef={forwardedRef}>
        {bubbleGroup2.map((Bubble, index) =>
          renderBubbles(Bubble, index, inViewport, 'bottom')
        )}
      </Container>
    );
  };

  const TopSection = handleViewport(TopSectionBlock);
  const BottomSection = handleViewport(BottomSectionBlock);

  return (
    <>
      <TopSection />
      <BottomSection />
    </>
  );
}
