import React, { useEffect } from 'react';
import anime from 'animejs/lib/anime';

const animOptions = {
  duration: 1500,
  direction: 'alternate',
  easing: 'linear',
  loop: true,
  autoplay: false,
};

export default function Bubble17({ isAnimate, toggleBubbleAnimation }) {
  useEffect(() => {
    const anime1 = anime({
      ...animOptions,
      targets: '#bubble-17-bg path',
      d: [
        {
          value:
            'M108.000,1.000 C182.121,-0.183 235.697,16.222 258.000,66.000 C275.223,104.440 270.845,159.047 240.000,180.000 C219.586,193.867 190.816,206.808 162.000,212.000 C67.453,229.034 -21.487,172.120 5.000,69.000 C10.000,57.001 15.000,44.999 20.000,33.000 C32.982,12.232 53.167,12.715 79.000,5.000 C88.666,3.667 98.334,2.333 108.000,1.000 Z',
        },
      ],
    });

    if (isAnimate) {
      anime1.play();
    } else {
      anime.remove('#bubble-17-bg path');
    }
  }, [isAnimate, toggleBubbleAnimation]);

  return (
    <button type="button" className="no-btn-styles bubble bubble-17">
      <svg
        className="bubble-item bubble-17-bg"
        xmlns="http://www.w3.org/2000/svg"
        width="268px"
        height="216px"
      >
        <clipPath id="bubble-17-bg">
          <path
            fill="rgb(0, 0, 0)"
            d="M108.000,1.000 C182.121,-0.183 235.697,16.222 258.000,66.000 C275.223,104.440 270.845,169.047 240.000,190.000 C219.586,203.867 190.816,206.808 162.000,212.000 C67.453,229.034 -21.487,192.120 5.000,89.000 C10.000,77.001 15.000,64.999 20.000,53.000 C32.982,32.232 53.167,12.715 79.000,5.000 C88.666,3.667 98.334,2.333 108.000,1.000 Z"
          />
        </clipPath>
      </svg>
      <img
        className="bubble-item bubble-17-img"
        src={require('../../assets/images/bubble-17-bg.jpg')}
        alt=""
      />
      <svg
        className="bubble-item bubble-17-shape"
        xmlns="http://www.w3.org/2000/svg"
        width="206px"
        height="86px"
      >
        <path
          fill="#ed315a"
          d="M141.000,1.000 C182.366,-0.141 224.426,26.918 196.000,63.000 C164.270,103.276 16.242,85.864 0.000,45.000 C0.000,41.667 0.000,38.333 0.000,35.000 C13.591,7.446 53.583,24.966 86.000,20.000 C108.316,16.582 117.981,3.521 141.000,1.000 Z"
        />
      </svg>
      <span className="bubble-item bubble-17-text">Comic Cosmetics</span>
    </button>
  );
}
