import React, { useEffect } from 'react';
import anime from 'animejs/lib/anime';

const animOptions = {
  duration: 1500,
  direction: 'alternate',
  easing: 'linear',
  loop: true,
  autoplay: false,
};

export default function Bubble21({ isAnimate, toggleBubbleAnimation }) {
  useEffect(() => {
    const anime1 = anime({
      ...animOptions,
      targets: '#bubble-21-bg path',
      d: [
        {
          value:
            'M86.000,7.000 C130.216,7.487 184.949,22.118 201.000,48.000 C244.268,117.770 215.798,234.609 160.000,250.000 C131.591,257.836 88.325,251.807 73.000,240.000 C36.326,211.745 -19.647,104.763 8.000,32.000 C15.195,13.065 30.171,7.228 50.000,1.000 C58.666,-0.333 76.596,4.275 86.000,7.000 Z',
        },
      ],
    });

    if (isAnimate) {
      anime1.play();
    } else {
      anime.remove('#bubble-21-bg path');
    }
  }, [isAnimate, toggleBubbleAnimation]);

  return (
    <button type="button" className="no-btn-styles bubble bubble-21">
      <svg
        className="bubble-item bubble-21-bg"
        xmlns="http://www.w3.org/2000/svg"
        width="236px"
        height="266px"
      >
        <clipPath id="bubble-21-bg">
          <path
            fill="rgb(0, 0, 0)"
            d="M75.000,0.000 C119.216,0.487 203.949,25.118 220.000,51.000 C263.268,120.770 204.798,247.609 149.000,263.000 C120.591,270.836 97.325,254.807 82.000,243.000 C45.326,214.745 -20.647,117.763 7.000,45.000 C14.195,26.065 29.171,10.228 49.000,4.000 C57.666,2.667 66.334,1.333 75.000,0.000 Z"
          />
        </clipPath>
        <image
          clipPath="url(#bubble-21-bg)"
          href={require('../../assets/images/bubble-21-bg.jpg')}
        />
      </svg>
      <svg
        className="bubble-item bubble-21-shape"
        xmlns="http://www.w3.org/2000/svg"
        width="142px"
        height="106px"
      >
        <path
          fill="rgb(13, 85, 142)"
          d="M69.000,1.000 C107.393,0.264 133.567,12.830 141.000,43.000 C147.251,68.370 122.254,114.598 97.000,104.000 C81.758,96.440 86.555,72.147 74.000,63.000 C59.385,52.351 11.604,62.253 2.000,47.000 C-9.611,15.438 45.009,3.996 69.000,1.000 Z"
        />
      </svg>
      <span className="bubble-item bubble-21-text">
        Unique &amp; Nice <br /> to Surprise
      </span>
    </button>
  );
}
